import React from "react";

type FormActionProps = {
  text: string;
};

const FormAction: React.FC<FormActionProps> = ({ text }) => {
  return (
    <>
      <button
        type="submit"
        className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-amber-500 hover:bg-amber-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-purple-500 mt-10"
      >
        {text}
      </button>
    </>
  );
};

export default FormAction;
